.recipe {
  border-radius: 20px;
  box-shadow: 0px 5px 5px 5px rgb(53, 19, 19);
  margin: 20px;
  align-items: center;
  background-color: rgb(65, 13, 26);
  display: flex;
  flex-direction: column;
  color: white;
  justify-content: space-around;
}
.image {
  margin: 10px;
  border-radius: 50%;
  background-position: center center;
  color: white;
}
.url {
  border-radius: 10px;
  padding: 15px;
  cursor: pointer;
  border: none;
  background-color: rgb(112, 90, 207);
  color: white;
  margin: 10px;
}
.ingredients {
  font-size: 20px;
}
