.App {
  background-position: center;
  background-size: cover;
  padding: 0%;
  font-family: "Montserrat", sans-serif;
}

.search-form {
  min-width: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.search-bar {
  width: 50%;
  border: none;
  padding: 10px;
  margin: 20px;
  border-radius: 10px;
}
.search-button {
  padding: 10px;
  border-radius: 10px;
  border: none;
  background-color: rgb(192, 67, 98);
  color: white;
  cursor: pointer;
}
.recipes {
  width: 30%;
  margin: auto;
  min-width: 40vh;
}
